.menu {
  position: fixed;
  top: 30px;
  right: 30px;
  width: 50px;
  height: 50px;
  background: #000 url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAC8AAAAfCAYAAAB3XZQBAAAABGdBTUEAALGPC/xhBQAAACBjSFJNAAB6JgAAgIQAAPoAAACA6AAAdTAAAOpgAAA6mAAAF3CculE8AAAABmJLR0QAAAAAAAD5Q7t/AAAACXBIWXMAAAsSAAALEgHS3X78AAAAM0lEQVRYw+3WsREAIAzEMML+Oz8FQxAu0gQuXUmyPrVfB4gHALjKVYofFg8AbRkz8dPiD+qQDAquHlwPAAAAAElFTkSuQmCC);
  background-size: 30px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 1000;
}

.menu_is-opening {
  background: #000 url(data:image/png;base64,);
  background-size: 25px;
  background-position: center;
  background-repeat: no-repeat;
}

@media (max-width: 991px) {
  .menu {
    right: 40px;
  }
}
.preview {
  padding: 100px;
  min-height: 100vh;
  position: relative;
  width: 100%;
}

.preview__imgSidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 700px;
  height: 100%;
}

.preview__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.preview__content-box {
  position: absolute;
  top: 0;
  right: 0;
  width: calc(100% - 700px);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px;
  box-sizing: border-box;
}

.preview__title {
  position: absolute;
  top: 40px;
  left: 100px;
  font-size: 1.2em;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: #333;
  color: #fff;
  padding: 0 5px;
}

.preview__greeting,
.preview__profession {
  font-size: 1.5em;
  color: #333;
  font-weight: 300;
}

.preview__greete {
  font-size: 1.5em;
  color: #00bcd4;
  font-weight: 700;
}

.preview__subtitle {
  padding: 10px 0;
  font-size: 3em;
  color: #333;
  line-height: 1em;
}

.preview__about {
  font-size: 1.1em;
  margin: 20px 0;
  font-weight: 300;
}

.preview__btn {
  background: #00bcd4;
  color: #fff;
  padding: 10px 30px;
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  letter-spacing: 2px;
}

@media (max-width: 1152px) {
  .preview__imgSidebar {
    width: 500px;
  }

  .preview__content-box {
    width: calc(100% - 500px);
  }
}

@media (max-width: 991px) {
  .preview__imgSidebar {
    display: none;
  }

  .preview__content-box {
    width: 100%;
    padding: 40px;
  }

  .preview__title {
    left: 40px;
  }
}
.sidebar {
  position: fixed;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: -100%;
  width: calc(100% - 700px);
  height: 100%;
  padding: 40px;
  transition: 0.5s;
  z-index: 100;
}

.sidebar_is-opening {
  right: 0;
}

.sidebar__menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.sidebar__links {
  list-style: none;
}

.sidebar__link {
  color: #fff;
  text-decoration: none;
  font-size: 2em;
  margin: 10px 0;
  font-weight: 300;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.sidebar__link:hover {
  color: #00bcd4;
}

.sidebar__button {
  border: 0;
  background-color: unset;
  color: #fff;
  text-transform: uppercase;
  font-size: 2em;
  margin: 30px 0;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
}

.sidebar__button:hover {
  color: #00bcd4;
}

@media (max-width: 991px) {
  .sidebar {
    width: 100%;
  }
}
.socials {
  position: absolute;
  left: 100px;
  bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.socials__links {
  margin-right: 10px;
  list-style: none;
}

.socials__img {
  width: 50px;
  height: 50px;
  transform: scale(0.5);
  opacity: 0.7;
}

.socials__img:hover {
  transform: scale(0.8);
  opacity: 1;
}

@media (max-width: 991px) {
  .socials {
    left: 40px;
  }
}
.skills {
  padding: 60px 0;
  background: #000;
  min-height: 100vh;
}

.skills__info {
  width: 100%;
  text-align: center;
}

.skills__title {
  position: relative;
  color: #fff;
  font-size: 1.8em;
  font-weight: 700;
  letter-spacing: 1px;
}

.skills__title::before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -10px;
  width: 60px;
  height: 4px;
  background: #fff;
}

.skills__subtitle {
  padding: 22px 0;
  max-width: 700px;
  color: #fff;
  display: inline-block;
}

.skills__content {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.skills__lists {
  list-style: none;
  display: grid;
  padding: 0 100px;
  max-width: 100%;
  grid-template-columns: repeat(auto-fit, minmax(90px, 250px));
  justify-items: center;
  grid-gap: 50px 150px;
  gap: 50px 150px;
}

.skills__list-item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.skills__img {
  width: 250px;
  object-fit: cover;
}

@media (max-width: 485px) {
  .skills__img {
    width: 180px;
  }
}
.portfolio {
  padding: 70px 0;
  min-height: 100vh;
}

.portfolio__info {
  width: 100%;
  text-align: center;
}

.portfolio__title {
  position: relative;
  color: #00dcb4;
  font-size: 1.8em;
  font-weight: 700;
  letter-spacing: 1px;
}

.portfolio__title::before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -10px;
  width: 60px;
  height: 4px;
  background: #000;
}

.portfolio__subtitle {
  padding: 22px 0;
  max-width: 700px;
  color: #333;
  display: inline-block;
}

.portfolio__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.portfolio__box {
  position: relative;
  width: 300px;
  height: 300px;
  margin: 90px;
  transition: 0.5s;
  text-align: center;
}

.portfolio__image-box,
.portfolio__text-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.portfolio__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.portfolio__text-box {
  background: rgba(0, 0, 0, 0.9);
  opacity: 0;
  transition: 0.5s;
}

.portfolio__box:hover .portfolio__text-box {
  opacity: 1;
}

.portfolio__link {
  text-decoration: none;
}

.portfolio__text {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1em;
}

.portfolio__text:hover {
  color: #00dcb4;
}

@media screen and (max-width: 1024px) {
  .portfolio__box {
    width: 260px;
    height: 260px;
    margin: 60px;
  }
}

@media screen and (max-width: 700px) {
  .portfolio__box {
    width: 250px;
    height: 250px;
    margin: 40px;
  }
}

@media screen and (max-width: 485px) {
  .portfolio__box {
    width: 180px;
    height: 180px;
    margin: 20px;
  }
}
.contact {
  padding: 100px 0;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
}

.contact__info {
  width: 100%;
  text-align: center;
}

.contact__title {
  position: relative;
  color: #fff;
  font-size: 1.8em;
  font-weight: 700;
  letter-spacing: 1px;
}

.contact__title::before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -10px;
  width: 60px;
  height: 4px;
  background: #fff;
}

.contact__subtitle {
  padding: 22px 0;
  max-width: 700px;
  color: #fff;
  display: inline-block;
}

.contact__form {
  position: relative;
  width: 700px;
  margin-top: 20px;
}

.contact__row {
  width: 100%;
  display: flex;
}

.contact__col {
  width: 50%;
  margin: 10px;
}

.contact__input,
.contact__text,
.contact__button {
  width: 100%;
  padding: 10px;
  border: none;
  outline: none;
  background: #111;
  color: #fff;
  font-size: 16px;
  resize: none;
}

.contact__input-error {
  font-size: 10px;
  line-height: 1.2;
  color: #ee3465;
}

.contact__text-box {
  width: 100%;
  margin: 10px;
  text-align: center;
}

.contact__text {
  height: 100px;
}

.contact__button {
  background: #00bcd4;
  color: #fff;
  padding: 10px 20px;
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  letter-spacing: 2px;
  cursor: pointer;
  max-width: 200px;
}

.contact__button_disabled {
  background-color: #9c929286;
  cursor: not-allowed;
}

@media (max-width: 991px) {
  .contact__form {
    width: 100%;
  }
}

@media (max-width: 485px) {
  .contact__row {
    flex-direction: column;
  }

  .contact__col {
    width: 100%;
    margin: 0;
    padding: 10px;
  }

  .contact__text-box {
    margin: 0;
    padding: 10px;
  }
}
.footer {
  color: #fff;
  background: #111;
  text-align: center;
  padding: 10px;
}

.footer__copyright {
  margin: 0;
  font-size: 16px;
}
.popup {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s, opacity 0.2s ease-in-out;
}

.popup_is-opening {
  visibility: visible;
  opacity: 1;
}

.popup__overlay {
  background-color: #000;
  opacity: 0.5;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.popup__form {
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0px;
  padding: 30px 20px;
  box-sizing: border-box;
  width: 240px;
  min-height: 180px;
}

.popup__close-button {
  position: absolute;
  top: -28px;
  left: calc(100% + 3px);
  background-image: url(data:image/png;base64,);
  background-size: contain;
  width: 25px;
  height: 25px;
  border: none;
  background-color: transparent;
  padding: 0;
}

.popup__close-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

.popup__close-button:focus {
  outline: none;
}

.popup__bold-text {
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  line-height: 22px;
  color: #000;
  text-align: center;
  margin: 0;
}

@media screen and (max-width: 500px) {
  .popup__form {
    width: 210px;
  }

  .popup__bold-text {
    font-size: 13px;
    line-height: 15px;
  }
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins','Montserrat', sans-serif;
  scroll-behavior: smooth;
}
