.popup {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s, opacity 0.2s ease-in-out;
}

.popup_is-opening {
  visibility: visible;
  opacity: 1;
}

.popup__overlay {
  background-color: #000;
  opacity: 0.5;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.popup__form {
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0px;
  padding: 30px 20px;
  box-sizing: border-box;
  width: 240px;
  min-height: 180px;
}

.popup__close-button {
  position: absolute;
  top: -28px;
  left: calc(100% + 3px);
  background-image: url(../../images/close.png);
  background-size: contain;
  width: 25px;
  height: 25px;
  border: none;
  background-color: transparent;
  padding: 0;
}

.popup__close-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

.popup__close-button:focus {
  outline: none;
}

.popup__bold-text {
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  line-height: 22px;
  color: #000;
  text-align: center;
  margin: 0;
}

@media screen and (max-width: 500px) {
  .popup__form {
    width: 210px;
  }

  .popup__bold-text {
    font-size: 13px;
    line-height: 15px;
  }
}